<template>
  <div class="bg">
    <b-container class="p-4">
      <b-row>
        <b-col>
          <b-form-input v-model="x" placeholder="Enter your current x coordinate..."></b-form-input>
        </b-col>
        <b-col>
          <b-form-input v-model="y" placeholder="Enter your current y coordinate..."></b-form-input>
        </b-col>
      </b-row>
      <b-row class="p-5">
        <div class="bg-dark text-white" style="height: 75vh; overflow-y: auto; white-space: pre">
          <p id="closest"></p>
        </div>
      </b-row>
    </b-container>
    <p style="position: absolute; bottom: 0.1vh; left: 1vw;" class="text-white">Created and maintained by Indecision#7334</p>
  </div>
</template>

<script>
import taverns from "../assets/sectors";

export default {
  name: "Sectors",
  data() {
    return {
      x:null,
      y:null,
      taverns: [],
      closestTaverns: null
    }
  },
  beforeCreate() {
    document.title = 'Tavern Finder'
    const linkElement = document.querySelector('link[rel=icon]');
    linkElement.href = 'tavernFavicon.ico'
  },
  created() {
    for (let tavern of taverns) {
      [tavern.x, tavern.y] = this.extractNumbers(tavern.system)
      tavern.notes = tavern.notes || 'No notes.'
      this.taverns.push(tavern)
    }
    console.log(taverns)
  },
  watch: {
    x: function() {
      this.updateClosest()
    },
    y: function() {
      this.updateClosest()
    },
    closestTaverns: function(val) {
      const closest = document.getElementById('closest')
      closest.innerHTML=val
    }
  },

  methods: {
    updateClosest() {
      if (!(this.y && this.x)) {
        return this.closestTaverns = ''
      }
      this.taverns.sort((first, second) => {
        if (this.getDistance(first) > this.getDistance(second)) {
          return 1
        }
        return -1
      })
      this.closestTaverns = ''
      this.taverns.forEach(tavern => {
        this.closestTaverns += 'Sector: '+tavern.sector+'\nSystem: '+tavern.system+'\nPlanet: '+tavern.planet+
            '\nGround: '+tavern.ground+'\nDistance: '+this.getDistance(tavern).toString()+
            '\nShielded: '+tavern.shielded+'\n<a href="'+tavern.planner+'">Travel Planner</a>'+'\nNotes: '+tavern.notes+'\n\n\n'
      })
    },
    getDistance(tavern) {
      let distance = 0
      distance += Math.abs(this.x-tavern.x)
      distance += Math.abs(this.y-tavern.y)
      return distance
    },

    extractNumbers(string) {
      return string.match(/[-+]?[0-9]*\.?[0-9]+/g).map(i => parseInt(i))
    }
  }
}
</script>

<style scoped>
.bg {
  background-image: url('https://cdn11.bigcommerce.com/s-tsmdqygn6r/images/stencil/original/products/728/5903/8591__91474.1524592011.jpg?c=2') !important;
  width: 100vw;
  height: 100vh;
  background-size: cover;
}
</style>
